import React, { Component } from 'react';
import axios from 'axios';
import './App.css';
import List from './List';

export default class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      term: '',
      items: [],
      semester: ''
    };
  }

  componentWillMount() {
    axios.get(`http://installfest.vtluug.org/api/v1/term`)
      .then(res => {
        this.setState({
          semester: res.data
        })
      });
  }

  onChange = (event) => {
    this.setState({ term: event.target.value });
  }

  onSubmit = (event) => {
    event.preventDefault();
    const term = this.state.term.toLowerCase().trim();
    axios.post("http://installfest.vtluug.org/api/v1/add/"+term).then( () => {
      console.log("posted")
      this.setState({
        term: '',
        items: [...this.state.items, this.state.term]
      });
    });
  }

  render() {
    return (
      <div classname="App" class="App">
        <div class="jumbotron">
          <h2>
            Welcome to the {this.state.semester} Installfest!
          </h2>
          <br/>
          <img width="200" alt="VTLUUG Logo" src={require("./assets/Crystal_Hokie_Tux.png")} />
        </div>
        <div class="container">
          <div class="row">
            <div class="col">
            </div>
            <div class="sm-col-6">
            <form class="input-group mb-3" onSubmit={this.onSubmit}>
              <input type="text" class="form-control" placeholder="What distro did you install?" value={this.state.term} onChange={this.onChange} />
              <span class="input-group-append">
              <button class="btn btn-outline-primary">Submit</button>
              </span>
            </form>
            <List items={this.state.items} />
            </div>
            <div class="col">
            </div>
          </div>
        </div>
      </div>
    );
  }
}



