import React, { Component } from 'react';
import axios from 'axios';
import io from 'socket.io-client';
import './App.css';

/*const List = props => (
  <ul>
    {
      props.items.map((item, index) => <li key={index}>{item}</li>)
    }
  </ul>
);
*/

export default class List extends Component {

  state = {
    distroarr:[]
  }

  constructor(props) {
    super(props);
    this.getDistros();
    //window.setInterval(this.getDistros(), 1000);

  }

  componentDidMount() { //update page every 30 seconds
    this.interval = setInterval(() => this.getDistros(), 30000);
    // var socket = io("//bgregos.me/distroupdates")
    // socket.on('event', data => {
    //   console.log("Server sent update...")
    //   var distro = Object.keys(data);
    //   var installs = Object.values(data);
    //   var newdistroarr = this.state.distroarr;
    //   for(var obj in newdistroarr){
    //     if(obj["distro"] === distro){
    //       obj["installs"] = installs;
    //     }
    //   }
    //   console.log("Server sent update: " + newdistroarr)
    //   this.setState({
    //     distroarr: newdistroarr
    //   });
    // });
  }
  componentWillUnmount() {
    clearInterval(this.interval);
  }

  getDistros() {
    axios.get(`http://installfest.vtluug.org/api/v1/distrolist`)
      .then(res => {
        var temp = res.data.distros;
        var temp_keys = Object.keys(temp);
        var temp_vals = Object.values(temp);
        console.log("alive1");
        var distros = [];
        console.log("alive2");
        for (var i = 0, len = temp_keys.length; i < len; i++) {
          distros.push({installs:temp_vals[i], distro:temp_keys[i]});
        }
        console.log("alive");
        console.log(distros, JSON.stringify(distros));
        this.setState({
          distroarr: distros
        });
      });
  }

  componentWillReceiveProps(nextProps){
     if(this.props.items !== nextProps.items){ //only update if needed
       console.log("Recieving new props.")
       this.getDistros(); //remove, replace with parsing props list
     }
  }

  compare(a, b) {
    if (a["installs"] > b["installs"]) {
      return -1;
    }
    if (a["installs"] < b["installs"]) {
      return 1;
    }
    // a must be equal to b
    return 0;
  }

  render(props) {
    var distroarr = this.state.distroarr;
    console.log(this.state.distroarr.sort(this.compare));
    return(
      <table class="table" >
        {
          distroarr.sort(this.compare).map((entry) => {
            const installs = entry["installs"];
            const distro = entry["distro"];
            return (<tr><td> {installs} </td> <td> {distro} </td></tr>);
          })
        }
      </table>
    );
  }
}



